export interface LeadData {
  name: string
  email: string
  company?: string
  jobTitle?: string
  industry?: string
  companySize?: string
  engagement: number
  source?: string
  websiteVisits?: number
  emailOpens?: number
  emailClicks?: number
  formSubmissions?: number
  phoneNumber?: string
  location?: string
}

export interface ScoringWeights {
  engagement: number
  companySize: number
  industry: number
  source: number
  behavior: number
}

const DEFAULT_WEIGHTS: ScoringWeights = {
  engagement: 0.3,
  companySize: 0.2,
  industry: 0.15,
  source: 0.15,
  behavior: 0.2,
}

const COMPANY_SIZE_SCORES: Record<string, number> = {
  'startup': 60,
  'small': 70,
  'medium': 85,
  'large': 95,
  'enterprise': 100,
}

const INDUSTRY_SCORES: Record<string, number> = {
  'technology': 90,
  'finance': 85,
  'healthcare': 80,
  'manufacturing': 75,
  'retail': 70,
  'education': 65,
  'other': 50,
}

const SOURCE_SCORES: Record<string, number> = {
  'website': 70,
  'referral': 90,
  'social-media': 60,
  'email-campaign': 65,
  'paid-ads': 55,
  'trade-show': 80,
  'cold-outreach': 40,
}

export function calculateLeadScore(
  leadData: Partial<LeadData>,
  weights: ScoringWeights = DEFAULT_WEIGHTS
): number {
  let totalScore = 0

  // Engagement score (0-100)
  const engagementScore = Math.min(leadData.engagement || 0, 100)
  totalScore += engagementScore * weights.engagement

  // Company size score
  const companySizeScore = COMPANY_SIZE_SCORES[leadData.companySize?.toLowerCase() || ''] || 50
  totalScore += companySizeScore * weights.companySize

  // Industry score
  const industryScore = INDUSTRY_SCORES[leadData.industry?.toLowerCase() || ''] || 50
  totalScore += industryScore * weights.industry

  // Source score
  const sourceScore = SOURCE_SCORES[leadData.source?.toLowerCase() || ''] || 50
  totalScore += sourceScore * weights.source

  // Behavioral score based on activities
  let behaviorScore = 0
  if (leadData.websiteVisits) {
    behaviorScore += Math.min(leadData.websiteVisits * 5, 30)
  }
  if (leadData.emailOpens) {
    behaviorScore += Math.min(leadData.emailOpens * 3, 25)
  }
  if (leadData.emailClicks) {
    behaviorScore += Math.min(leadData.emailClicks * 8, 30)
  }
  if (leadData.formSubmissions) {
    behaviorScore += Math.min(leadData.formSubmissions * 15, 40)
  }
  
  behaviorScore = Math.min(behaviorScore, 100)
  totalScore += behaviorScore * weights.behavior

  return Math.round(Math.min(totalScore, 100))
}

export function getScoreCategory(score: number): {
  category: string
  color: string
  description: string
} {
  if (score >= 80) {
    return {
      category: 'Hot',
      color: 'text-red-600',
      description: 'High-priority lead, ready for immediate contact'
    }
  } else if (score >= 60) {
    return {
      category: 'Warm',
      color: 'text-orange-600',
      description: 'Qualified lead, good potential for conversion'
    }
  } else if (score >= 40) {
    return {
      category: 'Cold',
      color: 'text-blue-600',
      description: 'Potential lead, needs nurturing'
    }
  } else {
    return {
      category: 'Low',
      color: 'text-gray-600',
      description: 'Low-priority lead, minimal engagement'
    }
  }
}
