"use client"

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { getScoreCategory } from '@/lib/leadScoring'

interface Lead {
  id: string
  name: string
  email: string
  company?: string
  jobTitle?: string
  industry?: string
  companySize?: string
  engagement: number
  source?: string
  websiteVisits?: number
  emailOpens?: number
  emailClicks?: number
  formSubmissions?: number
  phoneNumber?: string
  location?: string
  score: number
  createdAt: string
}

export default function LeadsPage() {
  const [leads, setLeads] = useState<Lead[]>([])
  const [filteredLeads, setFilteredLeads] = useState<Lead[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState('')
  const [searchTerm, setSearchTerm] = useState('')

  useEffect(() => {
    fetchLeads()
  }, [])

  useEffect(() => {
    if (searchTerm.trim() === '') {
      setFilteredLeads(leads)
    } else {
      const filtered = leads.filter(lead =>
        lead.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        lead.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (lead.company && lead.company.toLowerCase().includes(searchTerm.toLowerCase()))
      )
      setFilteredLeads(filtered)
    }
  }, [leads, searchTerm])

  const fetchLeads = async () => {
    try {
      setIsLoading(true)
      const response = await fetch('/api/leads')
      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch leads')
      }

      setLeads(data.leads || [])
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An unexpected error occurred')
    } finally {
      setIsLoading(false)
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getScoreBadgeVariant = (score: number) => {
    if (score >= 80) return 'destructive' // Hot - Red
    if (score >= 60) return 'default' // Warm - Default
    if (score >= 40) return 'secondary' // Cold - Secondary
    return 'outline' // Low - Outline
  }

  if (isLoading) {
    return (
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Leads</h1>
        </div>
        <div className="grid gap-4">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="animate-pulse">
              <CardContent className="p-6">
                <div className="h-4 bg-muted rounded w-1/4 mb-2"></div>
                <div className="h-3 bg-muted rounded w-1/2"></div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold">Leads</h1>
          <p className="text-muted-foreground">
            Manage and track your leads with intelligent scoring
          </p>
        </div>
        <Button asChild>
          <Link href="/leads/new">Add New Lead</Link>
        </Button>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Search and Stats */}
      <div className="flex flex-col sm:flex-row gap-4 items-center justify-between">
        <div className="w-full sm:w-auto">
          <Input
            placeholder="Search leads by name, email, or company..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full sm:w-80"
          />
        </div>
        <div className="text-sm text-muted-foreground">
          {filteredLeads.length} of {leads.length} leads
        </div>
      </div>

      {/* Leads Grid */}
      {filteredLeads.length === 0 ? (
        <Card>
          <CardContent className="p-12 text-center">
            <h3 className="text-lg font-semibold mb-2">
              {searchTerm ? 'No leads found' : 'No leads yet'}
            </h3>
            <p className="text-muted-foreground mb-4">
              {searchTerm 
                ? 'Try adjusting your search terms'
                : 'Get started by adding your first lead to the system'
              }
            </p>
            {!searchTerm && (
              <Button asChild>
                <Link href="/leads/new">Add Your First Lead</Link>
              </Button>
            )}
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4">
          {filteredLeads.map((lead) => {
            const scoreCategory = getScoreCategory(lead.score)
            return (
              <Card key={lead.id} className="hover:shadow-md transition-shadow">
                <CardContent className="p-6">
                  <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                    <div className="flex-1 space-y-2">
                      <div className="flex items-center gap-3">
                        <h3 className="text-lg font-semibold">{lead.name}</h3>
                        <Badge variant={getScoreBadgeVariant(lead.score)}>
                          {lead.score} - {scoreCategory.category}
                        </Badge>
                      </div>
                      
                      <div className="space-y-1 text-sm text-muted-foreground">
                        <p>{lead.email}</p>
                        {lead.company && (
                          <p>
                            {lead.company}
                            {lead.jobTitle && ` • ${lead.jobTitle}`}
                          </p>
                        )}
                        {lead.industry && (
                          <p className="capitalize">{lead.industry}</p>
                        )}
                      </div>
                    </div>

                    <div className="flex flex-col sm:items-end gap-2">
                      <div className="text-sm text-muted-foreground">
                        Added {formatDate(lead.createdAt)}
                      </div>
                      
                      {/* Engagement Metrics */}
                      <div className="flex gap-4 text-xs text-muted-foreground">
                        {lead.websiteVisits !== undefined && lead.websiteVisits > 0 && (
                          <span>{lead.websiteVisits} visits</span>
                        )}
                        {lead.emailOpens !== undefined && lead.emailOpens > 0 && (
                          <span>{lead.emailOpens} opens</span>
                        )}
                        {lead.emailClicks !== undefined && lead.emailClicks > 0 && (
                          <span>{lead.emailClicks} clicks</span>
                        )}
                        {lead.formSubmissions !== undefined && lead.formSubmissions > 0 && (
                          <span>{lead.formSubmissions} forms</span>
                        )}
                      </div>

                      {lead.source && (
                        <Badge variant="outline" className="text-xs">
                          {lead.source.replace('-', ' ')}
                        </Badge>
                      )}
                    </div>
                  </div>

                  {/* Score Description */}
                  <div className="mt-4 pt-4 border-t">
                    <p className="text-sm text-muted-foreground">
                      {scoreCategory.description}
                    </p>
                  </div>
                </CardContent>
              </Card>
            )
          })}
        </div>
      )}

      {/* Summary Stats */}
      {leads.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Lead Summary</CardTitle>
            <CardDescription>Overview of your lead pipeline</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="text-center">
                <div className="text-2xl font-bold">{leads.length}</div>
                <div className="text-sm text-muted-foreground">Total Leads</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">
                  {leads.filter(lead => lead.score >= 80).length}
                </div>
                <div className="text-sm text-muted-foreground">Hot Leads</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-orange-600">
                  {leads.filter(lead => lead.score >= 60 && lead.score < 80).length}
                </div>
                <div className="text-sm text-muted-foreground">Warm Leads</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold">
                  {Math.round(leads.reduce((sum, lead) => sum + lead.score, 0) / leads.length)}
                </div>
                <div className="text-sm text-muted-foreground">Avg Score</div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
